.gua-hitbox{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    
   
    z-index: 10000;
    pointer-events: none;
    font-size: 3em;
    flex-direction: row;
    flex-wrap: wrap;
}
.zone{
    width: 50%;
    height: calc(100% / 3);
    outline: 3px solid red;
}
.tian-zone,.banish-zone{
    width: 100%;
}
